<template>
  <div>
    <div style="display: flex; justify-content: space-between">
      <h1>MiniFeed</h1>
      <PaginationRow v-model="page" :pages="pages" />
    </div>

    <PostCard
      v-for="post in posts"
      :key="post.id"
      :author="post.author"
      :text="post.text"
      :created="formatDate(post.created)"
    />
  </div>
</template>

<script>
import PostCard from "@/components/PostCard.vue";
import PaginationRow from "@/components/PaginationRow.vue";
import { DateTime } from "luxon";
export default {
  components: {
    PostCard,
    PaginationRow,
  },
  data() {
    return {
      posts: [],
      page: 1,
      pages: 1,
    };
  },
  mounted() {
    this.fetchPosts();
  },
  methods: {
    fetchPosts() {
      this.$axios
        .get("/posts/", {
          params: {
            page: this.page,
            reversed: true,
          },
        })
        .then((response) => {
          this.posts = response.data.posts;
          this.pages = response.data.pages;
        })
        .catch((error) => console.error(error.message));
    },
    formatDate(date) {
      return DateTime.fromISO(date + "Z").toFormat("dd/MM/yyyy HH:mm");
    },
  },
  watch: {
    page() {
      this.fetchPosts();
    },
  },
};
</script>

<style lang="scss" scoped></style>
